<template>
	<div class="showblog">
		<h2>博客总览</h2>
		<input type="text" placeholder="搜索" v-model="search">
		<div v-for=" showblogUser in fliterdBlogs" >
			<router-link v-bind:to=" '/blog/'+showblogUser.id"><h3 v-colorRandom >{{ showblogUser.title | toUpcase  }}</h3></router-link>

			<p>{{showblogUser.conment | to-reduce }}</p>
		</div>

	</div>
</template>


<script>
	export default{
		name:'showblog',
		data(){
			return{
				showblogUsers:[],
				search:""
			}
		},
		
		directives:{
			"colorRandom":{
				bind(el,binding,vnode){
					el.style.color="#"+Math.random().toString(16).slice(2,8)
				}
			}
		},
		filters:{
			"toUpcase":function(value){
				return	value.toUpperCase()
			},
			

		},
		computed:{
			fliterdBlogs:function(){
				return this.showblogUsers.filter((blog)=>{
					return blog.title.match(this.search)
				})
			}
		},
		created(){
			this.$http.get("https://wd6814822638cajwsp.wilddogio.com/blog.json")
				.then((response)=>{
					console.log(response.json())
					return response.json()
					
				})
				.then((response)=>{
					// console.log(response)
					// var blogArray=[];
					for(let key in response){
						console.log(response[key])
						response[key].id=key
						this.showblogUsers.push(response[key])

						
					}
					console.log(this.showblogUsers)

				})
		},

	}
</script>

<style scoped>
	.showblog{
		max-width: 600px;
		margin: 0px auto;
		background:#7767EC;
		padding: 10px;
		box-sizing: border-box;

	}
	.showblog div{
		background: #ccc;
		margin: 30px 0px;
		padding: 15px;
	}
	.showblog h3{
		
		color:#F9450C;
	}
	input{
		width: 100%;
	}
	a{
		text-decoration: none;
	}
</style>


