<template>
  <div class="addblog" v-color:click="'yanse'">
  	<h2>添加博客</h2><button @click="blog.blogPreviewBool=!blog.blogPreviewBool" v-if="blog.blogPreviewBool">写博客</button>
	  <form @submit.prevent="post" v-if="!this.blog.blogPreviewBool" >
	   	
	   	<input type="text" v-model="blog.title" placeholder="填写标题名称">
	   	
		<h4 >博客内容</h4>
		<textarea cols="30" rows="10" v-model="blog.conment">
		</textarea><br>
		<label >Vue.js</label>
		<input type="checkbox" v-model="type"  value="Vue.js">
		<label >Node.js</label>
		<input type="checkbox" v-model="type"   value="Node.js">
		<label >React.js</label>
		<input type="checkbox" v-model="type"   value="React.js">  
		<label >Angular4</label>
		<input type="checkbox" v-model="type"    value="Angular4">
		<br>
		
		<label >作者</label>
		<select v-model="blog.authorName">
		<option v-for="author in blog.author">{{author}}</option>
		</select>
		<input type="submit" value="添加博客" >	

	 </form>
	<H4 v-if="this.blog.blogPreviewBool">你的博客添加成功</H4>	
   <hr>
	   <div id="blogPreview" v-if="this.blog.blogPreviewBool">
	   <h3>博客总览</h3>
	   <p>博客标题:{{blog.title}}</p>
	   <p>博客内容：{{blog.conment}}</p>
	   <p >微博分类: <p v-for=" type in type">{{type}}</p> </p>
	   <p>作者:{{blog.authorName }}</p>
		</div>
  </div>
</template>

<script>
export default {
  name: 'addblog',

  data (){
    return {
	      blog:{
	      	titil:"",
	      	conment:"",
	      	author:["linxiaopang","blue","wuyanzu"],
	      	blogPreviewBool:false,
	      	authorName:""
	      },
	      	type:[]
	    }
  },
  methods:{
  	post:function(){
  		this.$http.post("https://wd6814822638cajwsp.wilddogio.com/blog.json",{
  			title:this.blog.title,
  			conment:this.blog.conment,
  			author:this.blog.authorName,
  			type:this.type
  		})
  		.then((response)=>{
  			console.log(response)
  		})
  		this.blog.blogPreviewBool=true
  	}
  }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.addblog{
	width: 600px;
	margin:0 auto;
	text-align: center;
	

}
input[type="submit"]{
	background: blue;
	color: white;
	margin-top: 10px;
}
button{
	background: lightgreen;
	color: white;
	width: 75px;
	height: 75px;
	border-radius: 50%;
}
</style>



