<template>
	<div class="router" v-color>
		<router-link to="/" exact>博客</router-link>
  	<router-link to="/add" exact>写博客</router-link>

	</div>
</template>


<script>
export default{
	name:'router',
	directives:{
		"color":{
			bind(el,binging,vnode){
				el.style.background="#"+Math.random().toString(16).slice(2,8)
			}
		}
	}



}
</script>

<style scoped>
.router{
	text-align: center;
	width: 600px;
	margin:0 auto;
	padding: 20px;
	color: white;
	box-sizing: border-box;
	background: #E42121;
	margin-bottom: 20px;
}
a{
	color: white;
	text-decoration: none;
	padding: 5px;
	margin-right: 20px;
}
.router-link-active{
	background: white;
	color:lightgreen;
	border-radius: 5px;
	font-weight: bold;
}
</style>


