<template>
	<div class="showblog">
		<div >
			<h3>{{blogs.title |to-UpperCase}}</h3>
			<p>{{blogs.conment}}</p>
			<p>{{blogs.author}}</p>
			<ul>
			<li v-for="type in blogs.type">{{type}}</li>
			</ul>

		</div>

	</div>
</template>
<script>
	export default{
		name:'singleBlog',
		data(){
			return{
				id:this.$route.params.id,
				blogs:{}
			}
		},
		filters:{
			"to-UpperCase":function(value){
				return value.toUpperCase()
			}
		},
		created(){
			this.$http.get("https://wd6814822638cajwsp.wilddogio.com/blog/"+this.id+".json")
			.then((response)=>{
				console.log(response)
				return response.json()	
			}).then((response)=>{
				this.blogs=response
			})
		}
	}
</script>

<style scoped>
	.showblog{
		max-width: 600px;
		margin: 0px auto;
		background:#7767EC;
		padding: 10px;
		box-sizing: border-box;

	}
	.showblog div{
		background: #ccc;
		margin: 30px 0px;
		padding: 15px;
	}
	.showblog h3{
		
		color:#F9450C;
	}
	input{
		width: 100%;
	}
	a{
	}
</style>


